.advantages_list {
  @include reset-list;

  display: flex;
  justify-content: space-around;
  margin-top: 100px;
  padding: 50px 0 40px;

  box-shadow: 0 40px 80px rgba(0, 0, 0, 0.1);
}

.advantages_item-title {
  @include small-header;
}

.advantages_item-subtitle {
  @include bigger-main-text;
}

.advantages_item {
  padding-right: 120px;
  padding-left: 90px;

  border-right: 2px solid $basic-light-grey;

  &:last-child {
    padding-right: 0;

    border: none;
  }
}

.advantages_item--users {
  background-image: url("../img/user.png");
  background-repeat: no-repeat;
  background-position: left top;
}

.advantages_item--lacations {
  background-image: url("../img/location.png");
  background-repeat: no-repeat;
  background-position: left top;
}

.advantages_item--servers {
  background-image: url("../img/server.png");
  background-repeat: no-repeat;
  background-position: left top;
}

@media screen and (max-width: 991px) {
  .advantages_list {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    margin-top: 100px;
    padding: 50px 40px 40px;

    box-shadow: 0 40px 80px rgba(0, 0, 0, 0.1);
  }

  .advantages_item {
    padding: 0;
    padding-top: 80px;
    padding-left: 0;

    border: none;
  }

  .advantages_item-title,
  .advantages_item-subtitle {
    text-align: center;
  }

  .advantages_item--users,
  .advantages_item--lacations,
  .advantages_item--servers {
    background-position: center top;
  }
}

@media screen and (max-width: 767px) {
  .advantages_list {
    // flex-direction: column;
    justify-content: space-around;
  }
}
