.about-us {
  min-height: 420px;
  margin-top: 150px;

  background-image: url("../img/about-us.png");
  background-repeat: no-repeat;
  background-position: left top;
}

.about-us_title {
  @include medium-header;

  margin-top: 30px;
}

.about-us_subtitle {
  @include main-text;

  margin-top: 30px;
}

.about-us_list {
  @include small-text;
  @include reset-list;

  margin-top: 30px;
}

.about-us_list-item {
  margin-bottom: 20px;
  padding-left: 40px;

  background-image: url("../img/check.png");
  background-repeat: no-repeat;
  background-position: left center;
}

@media screen and (max-width: 991px) {
  .about-us {
    min-height: 700px;
    margin-top: 100px;

    background-position: center bottom;
  }
}

@media screen and (max-width: 545px) {
  .about-us_title {
    font-size: 30px;
  }
}

@media screen and (max-width: 440px) {
  .about-us_title {
    font-size: 25px;
  }
}
