.page-footer {
  margin-top: 100px;
  padding-top: 100px;
  padding-bottom: 50px;

  background-color: $basic-light-grey;
}

.footer {
  display: grid;

  grid-template-columns: 500px 1fr 1fr 1fr;
}

.page-footer_text {
  @include main-text;

  margin-top: 30px;

  & span {
    font-weight: 500;
  }
}

.page-footer_list-item-link {
  &:hover {
    opacity: 0.6;
  }
}

.page-footer_list {
  @include reset-list;

  display: flex;
}

.page-footer_copyright {
  @include main-text;

  opacity: 0.5;
}

.page-footer_product {
  @include medium-text;

  font-size: 18px;
}

.page-footer_product-list {
  @include reset-list;

  margin-top: 25px;
}

.page-footer_product-list-item-link {
  @include main-text;

  text-decoration: none;

  &:hover {
    color: $special-grey;
  }
}

.page-footer_product-list-item {
  margin-bottom: 15px;
}

.page-footer_logo-link {
  &:hover {
    opacity: 0.7;
  }
}

@media screen and (max-width: 991px) {
  .footer {
    grid-row-gap: 50px;
    grid-template-columns: 500px 1fr;
  }
}

@media screen and (max-width: 767px) {
  .footer {
    grid-row-gap: 50px;
    grid-template-columns: 400px 1fr;
  }
}

@media screen and (max-width: 570px) {
  .footer {
    grid-row-gap: 50px;
    grid-template-columns: 350px 1fr;
  }
}

@media screen and (max-width: 500px) {
  .footer {
    grid-row-gap: 50px;
    grid-template-columns: 1fr;
  }

  .page-footer_product-list,
  .page-footer_product,
  .footer-info {
    margin-left: 50px;
  }
}
