.map {
  margin-top: 100px;
}

.map_title {
  @include medium-header;

  text-align: center;
}

.map_subtitle {
  @include main-text;

  margin-top: 20px;

  text-align: center;

  & span {
    font-weight: 500;
  }
}

.map_img {
  display: block;
  margin: 0 auto;
  margin-top: 100px;
}

.map_list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.map_list-item {
  margin-right: 30px;

  &:last-child {
    margin-right: 0;
  }
}

@media screen and (max-width: 1199px) {
  .map_list-item {
    margin-right: 30px;
  }
}

@media screen and (max-width: 767px) {
  .map_list-item {
    width: 25%;
  }
}

@media screen and (max-width: 545px) {
  .map_title {
    font-size: 30px;
  }
}

@media screen and (max-width: 440px) {
  .map_title {
    font-size: 25px;
  }
}
