.subscribe {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;
  padding: 50px 70px;

  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
}

.subscribe_title {
  @include medium-header;

  margin-left: 50px;
}

.subscribe_subtitle {
  @include main-text;

  margin-top: 20px;
  margin-left: 50px;
}

.subscribe_button {
  margin-right: 50px;
  padding: 17px 65px;

  color: $basic-white;
  text-decoration: none;

  background-color: $basic-red;
  border-radius: 10px;

  &:hover {
    color: $basic-white;

    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
    opacity: 0.9;
  }
}

@media screen and (max-width: 991px) {
  .subscribe {
    margin-top: 50px;
    padding: 50px;
  }

  .subscribe_title {
    margin-left: 20px;
  }

  .subscribe_subtitle {
    margin-left: 20px;
  }

  .subscribe_button {
    margin-right: 20px;
  }
}

@media screen and (max-width: 767px) {
  .subscribe {
    flex-direction: column;
  }

  .subscribe_title {
    text-align: center;
  }

  .subscribe_subtitle {
    text-align: center;
  }
}

@media screen and (max-width: 545px) {
  .subscribe_title {
    font-size: 30px;
  }
}

@media screen and (max-width: 440px) {
  .subscribe_title {
    font-size: 25px;
  }
}
