.plans {
  margin-top: 100px;
  padding: 100px 0;

  background-color: $special-light-grey;
}

.plans_title {
  text-align: center;
}

.plans_subtitle {
  @include main-text;

  margin-top: 20px;

  text-align: center;
}

.plans_list {
  @include reset-list;

  display: grid;
  margin-top: 80px;

  grid-column-gap: 20px;
  grid-row-gap: 20px;
  grid-template-columns: 1fr 1fr 1fr;
}

.plans-catalog_item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 50px 0;

  background-color: $basic-white;
  border: 2px solid $basic-light-grey;
  border-radius: 10px;

  &:hover {
    border: 2px solid $basic-red;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  }
}

.plans-catalog_item-img {
  display: block;
  margin: 0 auto;
}

.plans-catalog_item-title {
  @include medium-text;

  margin-top: 30px;

  font-size: 18px;
  text-align: center;
}

.plans-catalog_item-list {
  @include reset-list;

  margin-top: 30px;
}

.plans-advantages_item {
  @include small-text;

  margin-bottom: 20px;
  padding-left: 40px;

  background-image: url("../img/check-small.png");
  background-repeat: no-repeat;
  background-position: left center;
}

.plans-catalog_item-price {
  @include medium-text;

  margin-top: 50px;

  font-size: 25px;
  text-align: center;

  & span {
    font-weight: 400;
    color: $basic-grey;
  }
}

.plans-catalog_item-button {
  @include medium-text;

  margin-top: 20px;
  padding: 13px 62px;

  font-weight: 800;
  color: $basic-red;

  background-color: transparent;
  border: 2px solid $basic-red;
  border-radius: 50px;

  &:hover {
    color: $basic-white;

    background-color: $basic-red;
  }
}

@media screen and (max-width: 991px) {
  .plans_list {
    grid-column-gap: 20px;
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 767px) {
  .plans_list {
    grid-column-gap: 20px;
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 440px) {
  .plans_title {
    font-size: 25px;
  }
}
