.page-header_nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 45px;
}

.site-menu_list {
  @include reset-list;

  display: flex;
}

.site-menu_item {
  margin-right: 40px;

  &:last-child {
    margin-right: 0;
  }
}

.navbar-toggler-icon {
  background-image: url("../img/icon-menu.png");
  background-repeat: no-repeat;
  background-position: center;

  &:hover {
    opacity: 0.7;
  }
}

.navbar-collapse {
  display: flex;
  justify-content: flex-end;
  width: auto;
}

.page-header_img {
  &:hover {
    opacity: 0.7;
  }
}

.site-menu_link {
  @include main-text;

  text-decoration: none;

  &:hover {
    color: $special-grey;
  }
}

.user-nav {
  @include reset-list;

  display: flex;
  justify-content: space-between;
  width: 230px;
  margin-left: 100px;
}


.user-nav_link {
  text-decoration: none;
}

.user-nav_link--sign-in {
  @include medium-text;

  &:hover {
    color: $special-grey;
  }
}

.user-nav_link--sign-up {
  @include medium-text;

  width: 150px;
  height: 45px;
  padding: 13px 45px;

  color: $basic-red;

  border: 1px solid $basic-red;
  border-radius: 50px;

  &:hover {
    color: $basic-grey;

    border: 1px solid $basic-grey;
  }
}

@media screen and (max-width: 991px) {
  .page-header {
    flex-wrap: wrap;
  }

  .navbar-expand-lg {
    flex-wrap: wrap;
  }

  .navbar-collapse {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
  }

  .site-menu_item {
    margin-bottom: 20px;
  }

  .user-nav {
    @include reset-list;

    display: flex;
    justify-content: flex-start;
    width: 100%;
    margin-top: 30px;
    margin-left: 0;
  }

  .user-nav_item {
    &:first-child {
      margin-right: 30px;
    }
  }

  .site-menu_list,
  .user-nav {
    width: 100%;
  }

  .site-menu_list {
    margin-top: 30px;
  }
}

@media screen and (max-width: 767px) {
  .site-menu_list,
  .user-nav {
    margin-left: 40px;
  }
}
