.testimonials {
  margin-top: 100px;
}

.testimonials_title {
  @include medium-header;

  text-align: center;
}

.testimonials_subtitle {
  @include main-text;

  margin-top: 20px;

  text-align: center;
}

.testimonials_list {
  @include reset-list;

  display: grid;
  box-sizing: border-box;
  margin-top: 60px;

  grid-gap: 30px;
  grid-template-columns: 1fr 1fr 1fr;
}

.testimonials_list-item {
  box-sizing: border-box;
  padding: 30px;

  border: 2px solid $special-grey;
  border-radius: 10px;

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    border: 2px solid $basic-red;
  }
}

.user-info {
  display: flex;
  align-items: center;
}

.testimonials_list-item-img {
  margin-right: 20px;
}

.testimonials_list-item-title {
  @include medium-text;

  margin-right: 50px;
  margin-bottom: 0;

  font-size: 18px;
}

.testimonials_list-item-subtitle {
  @include small-text;

  margin-bottom: 0;
}

.testimonials_list-item-mark {
  padding-right: 20px;

  font-size: 16px;

  background-image: url("../img/star.png");
  background-repeat: no-repeat;
  background-position: right center;
}

.testimonials_list-item-text {
  margin-top: 20px;
  margin-bottom: 0;

  font-size: 16px;
}

.testimonials-button {
  width: 60px;
  height: 60px;
  margin-top: 60px;
  margin-left: 20px;

  background-color: transparent;
  background-image: url("../img/arrow-left.png");
  background-repeat: no-repeat;
  background-position: center;
  border: 2px solid $basic-red;
  border-radius: 100%;

  &:hover {
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
    opacity: 0.9;
  }

  &:last-child {
    background-color: $basic-red;
    background-image: url("../img/arrow-right.png");
  }
}

@media screen and (max-width: 1199px) {
  .testimonials_list {
    grid-gap: 30px;
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 767px) {
  .testimonials_list {
    grid-gap: 30px;
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 545px) {
  .testimonials_title {
    font-size: 30px;
  }
}

@media screen and (max-width: 440px) {
  .testimonials_title {
    font-size: 25px;
  }
}
