.first-screen {
  min-height: 400px;

  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-position: top right;
}

.first-screen_title {
  @include big-header;

  margin-top: 120px;

  & span {
    font-weight: 800;
  }
}

.first-screen_subtitle {
  @include main-text;

  margin-top: 20px;
}

.first-screen_button {
  @include medium-text;

  width: 250px;
  height: 60px;
  margin-top: 50px;

  font-weight: 800;
  color: $basic-white;

  background-color: $basic-red;
  border: none;
  border-radius: 10px;

  &:hover {
    box-shadow: 0 20px 30px rgba(0, 0, 0, 0.1);
    opacity: 0.7;
  }
}

@media screen and (max-width: 991px) {
  .first-screen {
    min-height: 600px;

    background-position: center bottom;
  }

  .first-screen_title {
    margin-top: 80px;
  }
}

@media screen and (max-width: 545px) {
  .first-screen_title {
    font-size: 40px;
  }
}

@media screen and (max-width: 440px) {
  .first-screen_title {
    font-size: 25px;
  }
}
